@import '../../colors.sass'
          
.item
    font-weight: bold
    user-select: none
    &__link
        display: block
        padding: 20px 20px
        height: 100%
        width: 100%
        text-decoration: none
        transition: 0.3s
        &:hover
            background: rgba($color, 0.5)
            color: $background
