@import '../../colors.sass'
          
.records
    margin: 0 15px
    width: 100%
    border-radius: 10px
    box-shadow: 0 10px 40px rgba($color, 0.25)
    overflow: hidden
    &__title
        user-select: none
        font-weight: bold
        font-size: 2rem
        padding: 20px 20px
        height: 100%
        width: 100%
        background: rgba($color, 0.5)
        color: $background
