@import url(https://fonts.googleapis.com/css2?family=Montserrat+Alternates&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
.header{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:100%;max-width:1420px;padding:0 20px;margin:0 auto;height:60px;display:flex;align-items:center}.header__wrapper{width:100%;background:#fff;box-shadow:0 5px 10px rgba(0,0,0,.05)}.header__logo{height:100%;display:flex;align-items:center;font-weight:bold;margin-right:auto}
.item{font-weight:bold;-webkit-user-select:none;-ms-user-select:none;user-select:none}.item__link{display:block;padding:20px 20px;height:100%;width:100%;text-decoration:none;transition:.3s}.item__link:hover{background:rgba(38,55,76,.5);color:#f5f7fa}
.navbar{max-width:300px;width:100%;border-radius:10px;box-shadow:0 10px 40px rgba(38,55,76,.25);overflow:hidden}.navbar__title{-webkit-user-select:none;-ms-user-select:none;user-select:none;text-align:center;font-weight:bold;font-size:2rem;padding:20px 20px;height:100%;width:100%;background:rgba(38,55,76,.5);color:#f5f7fa}
.records{margin:0 15px;width:100%;border-radius:10px;box-shadow:0 10px 40px rgba(38,55,76,.25);overflow:hidden}.records__title{-webkit-user-select:none;-ms-user-select:none;user-select:none;font-weight:bold;font-size:2rem;padding:20px 20px;height:100%;width:100%;background:rgba(38,55,76,.5);color:#f5f7fa}




.app__container{display:grid;grid-template-columns:300px 1fr}
*{padding:0;margin:0;border:0}*,*::before,*::after{box-sizing:border-box}*:focus,*:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html,body{height:100%;width:100%;font-size:10px;line-height:1;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit}a{color:inherit}input::-ms-clear{display:none}button{cursor:pointer}button{-webkit-tap-highlight-color:rgba(0,0,0,0)}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}body{font-family:"Montserrat Alternates",sans-serif;font-size:1.6rem;color:#26374c;background:#f5f7fa}body::-webkit-scrollbar{width:1rem}body::-webkit-scrollbar-track{box-shadow:inset 0 0 5px rgba(38,55,76,.7)}body::-webkit-scrollbar-thumb{background-color:rgba(38,55,76,.7);border-radius:1rem}.container{width:100%;max-width:1400px;padding:0 10px;margin:30px auto 0;min-height:calc( 100vh - 60px )}.lnk{position:relative;transition:.2s}.lnk::before{content:"";background:#2bcbd7;width:100%;position:absolute;bottom:0;left:0;right:0;height:3px;transform:scaleX(0);transition:.2s}.lnk:hover{color:#2bcbd7}.lnk:hover::before{transform:scaleX(1)}
