@import '../../colors.sass'

.header
    user-select: none
    width: 100%
    max-width: 1420px
    padding: 0 20px
    margin: 0 auto
    height: 60px
    display: flex
    align-items: center
    &__wrapper
        width: 100%
        background: #fff
        box-shadow: 0 5px 10px rgba(#000, 0.05)
    &__logo
        height: 100%
        display: flex
        align-items: center
        font-weight: bold
        margin-right: auto